.home__hero-section {
    background-color:#f7f8fa;
    color:#fff;
    padding: 110px 0 30px 0;
}
.home__hero-row {
    align-items: center;
}
.row {
    display:flex;
    margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px;
    flex-wrap: wrap;
    align-content: stretch;
}
.col {
    margin-bottom: 15px;
    padding-right: 55px;
    padding-left: 55px;
    flex: 1;
    max-width: 50%;
    flex-basis: 50%;
}
.home__hero-text-wrapper {
    max-width: 540px;
    padding-top: O;
    padding-bottom: 60px;
}
.hero-section__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero-section__heading {
    color: #1c2237;
    margin: auto;
    margin-bottom: 24px;
    font-family: 'Qardoos-Personal-Use','Qardoos','Qardoos Personal Use Regular', Georgia, 'Times New Roman', Times, serif;
}
.hero-section__headingFive {
    margin: auto;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: black;
}

.top-line {
    color: goldenrod;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
}
.heading {
    margin-bottom: 24px;
    font-size: 28px;
    line-height: 1.1;
    font-weight: 600;
    color: #f7f8fa;
    font-family: 'Qardoos-Personal-Use','Qardoos','Qardoos Personal Use Regular', Georgia, 'Times New Roman', Times, serif;
}
.dark {
    color: black;
}
.darkBg {
    background: grey;
}

.home__hero-subtitle {
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
    list-style: none;
}
.home__hero-wrapper {
    max-width: 555px;
}
.home__hero-img {
    max-width: 95%;
    padding-right: 0;
    margin: 0 0 0 10px;
}
img {
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
}


@media screen and (max-width: 991px) {
    .container {
        padding-right: 30px;
        padding-left: 30px;
    }
}

@media screen and (max-width: 768px) {
    .home__hero-text-wrapper {
        padding-bottom: 65px;
    }
    .col {
        max-width: 100%;
        flex-basis: 100%;
    }
}