/* SLIDER CSS */
.wrapper {
	max-width: 1335px;
	box-sizing: border-box;
	padding: 0 20px;
	margin: auto;
	margin-top: -50px;
  }
  
  .wrapper a {
	display: inline-block;
	margin: 5px;
  }
  
  .wrapper a:first-child {
	margin-left: 0;
  }
  
  .slider-wrapper {
	position: relative;
	height: 34vh;
	overflow: hidden;
	background: url(../../Images/common/test-bg2.png) center/contain no-repeat;
  }
  
  .slide {
	height: 34vh;
	background-size: cover !important;
  }
  
  .slide::before {
	  content: '';
	  display: block;
	  position: absolute;
	  width: 100%;
	  height: 100%;
	  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
	  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
	  bottom: 0;
	  left: 0;
  }
  
  .previousButton, .nextButton {
		  position: absolute;
		  top: 50%;
		  transform: translateY(-50%);
		  z-index: 10;
		  background: url('http://svgshare.com/i/41_.svg') no-repeat center center / 16px;
		  width: 32px;
		  height: 32px;
		  text-indent: -9999px;
		  cursor: pointer;
  }
  
  .previousButton:hover, .nextButton:hover {
	background: url('http://svgshare.com/i/41_.svg') no-repeat center center / 16px;
  }
  
  .previousButton {
	left: 0;
	-webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
			transform: rotate(180deg) translateY(calc(50% + 0px));
  }
  
  .previousButton:hover {
	left: -10px;
  }
  
  .nextButton {
	right: 0;
  }
  
  .nextButton:hover {
	right: -10px;
  }
  
  .slider-content {
	text-align: center;
  }
  
  .slider-content .inner {
	padding: 0 70px;
	box-sizing: border-box;
	position: absolute;
	width: 100%;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
  }
  .slider-content .inner .title{
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-size: 100px;
	color: red;
	text-align: start;
	padding: 0 5px;
	width: 100%;
  }
  
  
  .slider-content .inner h5{
	font-weight: 200;
	margin: 0 auto;
	margin-top: -50px;
	max-width: 840px;
	color: black;
	font-size: 24px;
	line-height: 1;
  }
  
  .slider-content .inner p {
	color: black;
	font-size: 14px;
	line-height: 1.5;
	margin: 20px auto 30px;
	max-width: 640px;
  }
  
  .slider-content section {
	position: absolute;
	bottom: 20px;
	left: 20px;
  }
  
  .slider-content section span {
	color: #FFFFFF;
  }
  
  .slider-content span .user {
	color: rgba(255, 255, 255, 0.5);
	font-size: 22px;
	display: inline-block; 
	align-content: flex-start;
	line-height: 1.4;
	vertical-align: middle;
	margin-right: 0px;
	margin-top: 5px;
  }
  
  /* .slider-content section img {
	width: 40px;
	height: 40px;
	border: solid 2px rgba(255, 255, 255, 0.5);
	border-radius: 100%;
	vertical-align: middle;
  } */
  
  /* .slider-content span strong {
	color: black;
	font-size: 14px;
	display: block;
	align-content: flex-start;
  } */
  
  @media (max-height: 500px) {
	.slider-wrapper, .slide {
	  height: calc(80vh - 75px);
	}    
  }
  
  @media (max-width: 640px) {
	.slider-wrapper, .slide {
	  height: calc(70vh - 75px);
	}    
  }
  
  @media (max-height: 600px) {
	.slider-content .inner h5 {
	  font-size: 22px;
	}    
  }
  
  @media (max-width: 640px) {
	.slider-content .inner h5 {
	  font-size: 22px;
	}
  }
















/* Slider Animation CSS */

.slide h1 {
	transition: all 0.3s ease;
	-webkit-transform: translateY(-20px);
					transform: translateY(-20px);
	opacity: 0;
}

 .slide button {
	transition: all 0.3s ease;
	-webkit-transform: translateY(20px);
					transform: translateY(20px);
	opacity: 0;
} 
/* 
.slide p {
	transition: all 0.3s ease;
	-webkit-transform: translateY(20px);
					transform: translateY(20px);
	opacity: 0;
} */

.slide section * {
	transition: all 0.3s ease;
}

/* .slide section img {
	-webkit-transform: translateX(-10px);
					transform: translateX(-10px);
	opacity: 0;
} */

.slide section span {
	-webkit-transform: translateY(-10px);
					transform: translateY(-10px);
	opacity: 0;
}

/* .slide section span strong {
	-webkit-transform: translateY(10px);
					transform: translateY(10px);
	opacity: 0;
} */

.slide.animateIn.previous h1,
.slide.current h1,
.slide.animateIn.next h1,
.slide.animateIn.previous button,
.slide.current button,
.slide.animateIn.next button,
.slide.animateIn.previous p,
.slide.current p,
.slide.animateIn.next p,
.slide.animateIn.previous section *,
.slide.current section *,
.slide.animateIn.next section * {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	-webkit-transition-delay: .9s;
					transition-delay: .9s;
					opacity: 1;
}

.slide.animateIn.previous p,
.slide.animateIn.next p {
	-webkit-transition-delay: 1.1s;
					transition-delay: 1.1s;
}

.slide.animateIn.previous button,
.slide.animateIn.next button {
	-webkit-transition-delay: 1.3s;
					transition-delay: 1.3s;
}

.slide.animateIn.previous section img,
.slide.animateIn.next section img {
	-webkit-transition-delay: 1.3s;
					transition-delay: 1.3s;
}

.slide.animateIn.previous section span,
.slide.animateIn.next section span {
	-webkit-transition-delay: 1.4s;
					transition-delay: 1.4s;
}

.slide.animateIn.previous section span strong,
.slide.animateIn.next section span strong {
	-webkit-transition-delay: 1.5s;
					transition-delay: 1.5s;
}

.slide.animateOut h1 {
	-webkit-transition-delay: .3s;
					transition-delay: .3s;
}

.slide.animateOut p {
	-webkit-transition-delay: .2s;
					transition-delay: .2s;
}

.slide.animateOut section span {
	-webkit-transition-delay: .1s;
					transition-delay: .1s;
}

.slide.animateOut section span strong {
	-webkit-transition-delay: 0s;
					transition-delay: 0s;
}